import Vue from 'vue'
//import md5 from 'md5'
import { router, store } from '@/main.js'
import Config from '@/Config'

export default {
    request(api_name, params, method) {
        if(this.getCookie("saveUserInfo")!="" &&!this.getCookie("saveUserInfo")){
            router.push('/login')
            return
        }
        if (typeof (Config.api[api_name]) == 'undefined') {
            this.alertError('接口' + api_name + '未定义');
            return false;
        }
        let api_url = Config.apiHost + Config.api[api_name]
        let unauth = ['login', 'home', 'register', 'code', 'forget']
        if (unauth.indexOf(api_name) == -1) {
            //sign
            // params = this.sign(params)
        }
        if (!method) {
            method = 'get'
        }
        method = method.toLowerCase()
        switch (method) {
            case 'post':
                return new Promise((resolve, reject) => {
                    Vue.http.post(api_url, params)
                        .then(res => {
                            if (res.data.code == 1) {
                                resolve(res.data);
                            } else {
                                this.alertError(res.data.message);
                                reject(res.data.message);
                            }
                        })
                        .catch(error => {
                            if (error.response) {
                                if (error.response.status == 422) {
                                    let errorMsg = [];
                                    for (const key in error.response.data.errors) {
                                        if (error.response.data.errors.hasOwnProperty(key)) {
                                            let str = error.response.data.errors[key][0];
                                            errorMsg.push(str)
                                        }
                                    }
                                    this.alertError(errorMsg.join(';'));
                                    reject(errorMsg.join(';'));
                                } else {
                                    this.alertError(error.response.data.message || "无法连接服务器");
                                    reject(error.response.data.message);
                                }
                            } else {
                                this.alertError("无法连接服务器");
                                reject("无法连接服务器");
                            }
                        })
                })
            default:
                return new Promise((resolve, reject) => {
                    Vue.http.get(api_url, { params })
                        .then(res => {
                            if (res.data.code == 1) {
                                resolve(res.data);
                            } else {
                                this.alertError(res.data.message);
                                reject(res.data.message);
                            }
                        })
                        .catch(error => {
                            if (error.response) {
                                if (error.response.status == 422) {
                                    let errorMsg = [];
                                    for (const key in error.response.data.errors) {
                                        if (error.response.data.errors.hasOwnProperty(key)) {
                                            let str = error.response.data.errors[key][0];
                                            errorMsg.push(str)
                                        }
                                    }
                                    this.alertError(errorMsg.join(';'));
                                    reject(errorMsg.join(';'));
                                } else {
                                    this.alertError(error.response.data.message || "无法连接服务器");
                                    reject(error.response.data.message);
                                }
                            } else {
                                this.alertError("无法连接服务器");
                                reject("无法连接服务器");
                            }
                        })
                })
        }
    },
    sign(params) {
        params.token = this.getStorage('token', '')
        return params
    },
    getStorage(name, val) { // val: name对应的值不存在，默认返回val
        let v = sessionStorage.getItem(name), w
        if (v == null) {
            return val
        }
        try {
            w = JSON.parse(v)
            return w
        } catch (e) {
            return v
        }
    },
    setStorage(name, data) {
        sessionStorage.setItem(name, JSON.stringify(data))
        return data
    },
    alertError(msg, fb) {
        if (msg == 'token不能为空') {
            if (['', '/login', '/register'].indexOf(store.state.route.path) == -1) {
                this.setStorage('login_jump', store.state.route.path)
            }
            router.replace('/login')
            return false
        }
        let alert = {
            show: true,
            title: '错误',
            content: msg,
            onShow: () => {
            },
            onHide: () => {
                if (msg == '登录过期') {
                    if (['', '/login', '/register'].indexOf(store.state.route.path) == -1) {
                        this.setStorage('login_jump', store.state.route.path)
                    }
                    router.replace('/login')
                    return false
                }
                fb && fb()
            },
        }
        store.dispatch('global/hideLoading')
        store.commit('global/updateAlert', { alert })
    },
    getCookie(cookieName) {
        let strCookie = document.cookie;
        let arrCookie = strCookie.split("; ");
        for (let i = 0; i < arrCookie.length; i++) {
          let arr = arrCookie[i].split("=");
          if (cookieName == arr[0]) {
            return arr[1];
          }
        }
        return "";
    },
    setCookie(cname, cvalue, exdays) {
        let d = new Date();
        d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
        let expires = "expires=" + d.toUTCString();
        document.cookie = cname + "=" + cvalue + "; " + expires+"; path=/"
    },
    clearCookie(name) {
        this.setCookie(name, "", -1);
    },
    parseTime(time, cFormat, noZero) {
        noZero = noZero || false;
        if (arguments.length === 0) {
            return null
        }

        if ((time + '').length === 10) {
            time = +time * 1000
        }

        const format = cFormat || '{y}-{m}-{d} {h}:{i}:{s}'
        let date;
        if (typeof time === 'object') {
            date = time
        } else {
            date = new Date(parseInt(time))
        }
        const formatObj = {
            y: date.getFullYear(),
            m: date.getMonth() + 1,
            d: date.getDate(),
            h: date.getHours(),
            i: date.getMinutes(),
            s: date.getSeconds(),
            a: date.getDay()
        }
        const time_str = format.replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
            let value = formatObj[key]
            if (key === 'a') return ['一', '二', '三', '四', '五', '六', '日'][value - 1]
            if (!noZero && result.length > 0 && value < 10) {
                value = '0' + value
            }
            return value || 0
        })
        return time_str
    },
    formatFloat(num, digit) {
        var m = Math.pow(10, digit);
        return Math.round(num * m, 10) / m;
    },
    // 正则检验数据
    checkData(value, type) {
        let reg = {
            phone: /^(((13[0-9]{1})|(14[0-9]{1})|(17[0]{1})|(15[0-3]{1})|(15[5-9]{1})|(18[0-9]{1}))+\d{8})$/, //手机号
            digit: /(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/,//带小数的数字
            int: /^[1-9]\d*$/,//整数
            idcard: /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/,// 身份证号码为15位或者18位，15位时全为数字，18位前17位为数字，最后一位是校验位，可能为数字或字符X
            chinese: /^[\u4e00-\u9fa5]+$/,//中文字符
        };

        if (reg[type].test(value)) {
            return true
        } else {
            return false
        }
    }
}



