
import ViewBox from 'vux/src/components/view-box/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mapState, mapActions} from "vuex";

export default {
components: {
ViewBox,
Loading,
Alert,
Toast
},
methods: {},
mounted() {

},
beforeDestroy() {
},
computed: {
...mapState({
route: state => state.route,
alert: state => state.global.alert,
toast: state => state.global.toast
}),
title() {
if (this.route.meta.title) return this.route.meta.title;
if (this.route.name) return this.route.name;
return "";
}
},
watch: {

title: function (val) {
document.title = val;
}
},
data() {
return {};
}
};



