import Vue from 'vue'
import App from './App.vue'
import store from './store'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import { sync } from 'vuex-router-sync'
import Vuex from 'vuex'
import VueResource from 'vue-resource'
import VueScroller from 'vue-scroller' 
import VueLazyload from 'vue-lazyload' 
import VuePreview from 'vue-photo-preview' 
import 'vue-photo-preview/dist/skin.css'
import VueClipboard from 'vue-clipboard2' 

import TransferDom from 'vux/src/directives/transfer-dom/index.js'
 
import WechatPlugin from 'vux/src/plugins/wechat/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'


Vue.use(Vuex)
Vue.use(VueRouter)
Vue.use(VueScroller)
Vue.use(VueResource);
Vue.use(VueLazyload, {
error: require("../static/img/error.jpg"),
loading: require("../static/img/loading.gif")
});
Vue.use(VuePreview, {
fullscreenEl:false
})
Vue.use(VueClipboard)
Vue.use(WechatPlugin)
Vue.use(ToastPlugin)
Vue.use(DatetimePlugin)
Vue.directive('transfer-dom', TransferDom)


FastClick.attach(document.body)




/**
* -------------------------- 路由 ----------------------
*/


let routes = [{
                                path: '/',
                                meta: {title: '金淘', keepAlive: true},
                                component: function (resolve) {
                                  require(['./pages/Home.vue'], resolve)
                                }
                              },
{
                                path: '/mine',
                                meta: {title: '个人中心', keepAlive: false},
                                component: function (resolve) {
                                  require(['./pages/Mine.vue'], resolve)
                                }
                              },
{
                                path: '/mine2',
                                meta: {title: '个人中心', keepAlive: false},
                                component: function (resolve) {
                                  require(['./pages/Mine2.vue'], resolve)
                                }
                              },
{
                                path: '/withdrawlist',
                                meta: {title: '提现记录', keepAlive: false},
                                component: function (resolve) {
                                  require(['./pages/Withdrawlist.vue'], resolve)
                                }
                              },
{
                                path: '/applycash',
                                meta: {title: '申请提现', keepAlive: false},
                                component: function (resolve) {
                                  require(['./pages/Applycash.vue'], resolve)
                                }
                              },
{
                                path: '/friendscircle',
                                meta: {title: '朋友圈', keepAlive: true},
                                component: function (resolve) {
                                  require(['./pages/Friendscircle.vue'], resolve)
                                }
                              },
{
                                path: '/goodsdetail',
                                meta: {title: '商品详情', keepAlive: false},
                                component: function (resolve) {
                                  require(['./pages/Goodsdetail.vue'], resolve)
                                }
                              },
{
                                path: '/staff',
                                meta: {title: '所属客服', keepAlive: false},
                                component: function (resolve) {
                                  require(['./pages/Staff.vue'], resolve)
                                }
                              },
{
                                path: '/productlist',
                                meta: {title: '聚划算', keepAlive: true},
                                component: function (resolve) {
                                  require(['./pages/Productlist.vue'], resolve)
                                }
                              },
{
                                path: '/searchcoupon',
                                meta: {title: '全网搜券', keepAlive: false},
                                component: function (resolve) {
                                  require(['./pages/Searchcoupon.vue'], resolve)
                                }
                              },
{
                                path: '/goodssearch',
                                meta: {title: '搜索', keepAlive: false},
                                component: function (resolve) {
                                  require(['./pages/Goodssearch.vue'], resolve)
                                }
                              },
{
                                path: '/history',
                                meta: {title: '浏览记录', keepAlive: false},
                                component: function (resolve) {
                                  require(['./pages/History.vue'], resolve)
                                }
                              },
{
                                path: '/collection',
                                meta: {title: '收藏夹', keepAlive: false},
                                component: function (resolve) {
                                  require(['./pages/Collection.vue'], resolve)
                                }
                              },
{
                                path: '/order',
                                meta: {title: '订单管理', keepAlive: false},
                                component: function (resolve) {
                                  require(['./pages/Order.vue'], resolve)
                                }
                              },
{
                                path: '/income',
                                meta: {title: '收益', keepAlive: false},
                                component: function (resolve) {
                                  require(['./pages/Income.vue'], resolve)
                                }
                              },
{
                                path: '/earn',
                                meta: {title: '我要挣钱', keepAlive: false},
                                component: function (resolve) {
                                  require(['./pages/Earn.vue'], resolve)
                                }
                              },
{
                                path: '/fanslist',
                                meta: {title: '推广用户', keepAlive: false},
                                component: function (resolve) {
                                  require(['./pages/Fanslist.vue'], resolve)
                                }
                              },
{
                                path: '/qalist',
                                meta: {title: '常见问题', keepAlive: false},
                                component: function (resolve) {
                                  require(['./pages/Qalist.vue'], resolve)
                                }
                              },
{
                            path: '*',
                            component: function (resolve) {
                              require(['./components/NotFoundComponent.vue'], resolve)
                            }
                          }]
const router = new VueRouter({
routes
})

sync(store, router)


new Vue({
store,
router,
render: h => h(App)
}).$mount('#app')

export { router, store }



