import tool from '@/utils/tool'
// initial state
const state = {
    list: [],
    buyer_rate: 0,
    items: tool.getStorage("GoodsItem"),//当前商品项
}

// mutations 同步事务，接收数据->数据整形->改变state变量
const mutations = {
    updateList(state, data) {
        state.list = data
    },
    updateBuyerRate(state, data) {
        state.buyer_rate = data
    },
    updateSpreadRate(state, data) {
        state.spread_rate = data
    },
    updateGoodItems(state, data) {
        state.items = data
    },
}

// actions  异步事务，发起api请求->接收数据->提交到mutation
const actions = {
    getList({commit}, {params, cb}) {
        params = params || {}
        tool.request('goodsList',params).then((res)=>{
            commit('updateList', res.data)
            commit('updateBuyerRate', res.buyer_rate)
            commit('updateSpreadRate', res.spread_rate)
            cb && cb(res.data)
        })
    },
    loadMore({commit}, {params, cb}) {
        params = params || {}
        tool.request('goodsList',params).then((res)=>{
            let data = state.list.concat(res.data)
            commit('updateList', data)
            commit('updateBuyerRate', res.buyer_rate)
            commit('updateSpreadRate', res.spread_rate)
            cb && cb(data)
        })
    },
}

export default {
    namespaced: true, //开启命名空间，避免不同module下的actions和mutations重名
    state,
    mutations,
    actions
}



