import tool from '@/utils/tool'

// initial state
const state = {
    items: {
        data: [],
        page: 1,
        nomore: false,
        loaded: false
    }
}


// mutations 同步事务
const mutations = {
    updateList(state, data) {
        state.items = data
    },
    updatePage(state, data) {
        state.items.page = data
    }
}

// actions  异步事务
const actions = {
    setPage({commit}, page) {
        commit('updatePage', page)
    },

    getList({commit}, {params, cb}) {
        params = params || {}
        tool.request('historyList', params).then((res) => {
            let rs = {
                data: state.items.data.concat(res.data),
                loaded: true,
                page: state.items.page,
                nomore: res.data.length < 10,
            }
            commit('updateList', rs)
            cb && cb(res.data)
        })
    },

    loadMore({commit, dispatch}, cb) {
        if (!state.items.loaded) {
            // 等待第一页加载完成
            cb && cb()
            return false
        }
        dispatch('getList', {
            page: state.items.page++
        })
    },
}

export default {
    // 开启命名空间，避免不同module下的actions和mutations重名
    namespaced: true,
    state,
    mutations,
    actions
}



