// initial state
const state = {
    scrollTop: 0,
    isLoading: false,
    direction: 'forward',
    alert: {
        show: false,
        title: '',
        content: '',
        onShow: function () {
        },
        onHide: function () {
        }
    },
    toast: {
        show: false,
        text: ''
    },
}

// mutations
const mutations = {
    updatePosition(state, payload) {
        state.scrollTop = payload
    },
    updateLoadingStatus(state, payload) {
        state.isLoading = payload.isLoading
    },
    updateDirection(state, payload) {
        state.direction = payload.direction
    },
    updateAlert(state, payload) {
        state.alert = payload.alert
    },
    updateToast(state, payload) {
        state.toast = payload
    },
}

// actions
const actions = {
    updatePosition({commit}, data) {
        commit('updatePosition', data)
    },
    showLoading({commit}) {
        commit('updateLoadingStatus', {isLoading: true})
    },
    hideLoading({commit}) {
        commit('updateLoadingStatus', {isLoading: false})
    },
    showToast({commit}, text) {
        commit('updateToast', {text: text, show: true})
    }
}


export default {
    namespaced: true,
    state,
    actions,
    mutations
}



